	module.exports = {"data":{"postsJson":{"title":"예수님께서 그의 얼굴을 예루살렘으로 향하시다","questionsTitle":"더 깊이 들어감","display":"block","bookName":"누가복음","ref":"9:51-11:54","html":"<p>   <strong>9</strong><u>51</u>예수께서 승천하실 기약이 차가매 예루살렘을 향하여 올라가기로 굳게 결심하시고 <u>52</u>사자들을 앞서 보내시매 그들이 가서 예수를 위하여 준비하려고 사마리아인의 한 마을에 들어갔더니 <u>53</u>예수께서 예루살렘을 향하여 가시기 때문에 그들이 받아들이지 아니 하는지라 <u>54</u>제자 야고보와 요한이 이를 보고 이르되</p><h1>제자 야고보와 요한</h1><h3>주여 우리가 불을 명하여 하늘로부터 내려 저들을 멸하라 하기를 원하시나이까</h3><p><u>55</u>예수께서 돌아보시며 꾸짖으시고 <u>56</u>함께 다른 마을로 가시니라<br />   <u>57</u>길 가실 때에 어떤 사람이 여짜오되</p><h1>길 가던 어떤 사람</h1><h4>어디로 가시든지 나는 따르리이다</h4><p><u>58</u>예수께서 이르시되</p><h1>예수님</h1><h2>여우도 굴이 있고 공중의 새도 집이 있으되 인자는 머리 둘 곳이 없도다</h2><p>하시고 <u>59</u>또 다른 사람에게 나를 따르라 하시니 그가 이르되</p><h1>길 가던 어떤 사람</h1><h4>나로 먼저 가서 내 아버지를 장사하게 허락하옵소서</h4><p><u>60</u>이르시되</p><h1>예수님</h1><h2>죽은 자들로 자기의 죽은 자들을 장사하게 하고 너는 가서 하나님의 나라를 전파하라</h2><p>하시고 <u>61</u>또 다른 사람이 이르되</p><h1>길 가던 어떤 사람</h1><h4>주여 내가 주를 따르겠나이다마는 나로 먼저 내 가족을 작별하게 허락하소서</h4><p><u>62</u>예수께서 이르시되</p><h1>예수님</h1><h2>손에 쟁기를 잡고 뒤를 돌아보는 자는 하나님의 나라에 합당하지 아니하니라</h2><p>하시니라<br />   <strong>10</strong><u>1</u>그 후에 주께서 따로 칠십 인을 세우사 친히 가시려는 각 동네와 각 지역으로 둘씩 앞서 보내시며 <u>2</u>이르시되</p><h1>예수님</h1><h2>추수할 것은 많되 일꾼이 적으니 그러므로 추수하는 주인에게 청하여 추수할 일꾼들을 보내 주소서 하라 <u>3</u>갈지어다 내가 너희를 보냄이 어린 양을 이리 가운데로 보냄과 같도다 <u>4</u>전대나 배낭이나 신발을 가지지 말며 길에서 아무에게도 문안하지 말며 <u>5</u>어느 집에 들어가든지 먼저 말하되 이 집이 평안할지어다 하라 <u>6</u>만일 평안을 받을 사람이 거기 있으면 너희의 평안이 그에게 머물 것이요 그렇지 않으면 너희에게로 돌아오리라 <u>7</u>그 집에 유하며 주는 것을 먹고 마시라 일꾼이 그 삯을 받는 것이 마땅하니라 이 집에서 저 집으로 옮기지 말라<br />   <u>8</u>어느 동네에 들어가든지 너희를 영접하거든 너희 앞에 차려놓는 것을 먹고 <u>9</u>거기 있는 병자들을 고치고 또 말하기를 하나님의 나라가 너희에게 가까이 왔다 하라 <u>10</u>어느 동네에 들어가든지 너희를 영접하지 아니하거든 그 거리로 나와서 말하되 <u>11</u>너희 동네에서 우리 발에 묻은 먼지도 너희에게 떨어버리노라 그러나 하나님의 나라가 가까이 온 줄을 알라 하라 <u>12</u>내가 너희에게 말하노니 그 날에 소돔이 그 동네보다 견디기 쉬우리라<br />   <u>13</u>화 있을진저 고라신아, 화 있을진저 벳새다야, 너희에게 행한 모든 권능을 두로와 시돈에서 행하였더라면 그들이 벌써 베옷을 입고 재에 앉아 회개하였으리라 <u>14</u>심판 때에 두로와 시돈이 너희보다 견디기 쉬우리라 <u>15</u>가버나움아 네가 하늘에까지 높아지겠느냐 음부에까지 낮아지리라 <u>16</u>너희 말을 듣는 자는 곧 내 말을 듣는 것이요 너희를 저버리는 자는 곧 나를 저버리는 것이요 나를 저버리는 자는 나 보내신 이를 저버리는 것이라</h2><p>하시니라<br />   <u>17</u>칠십 인이 기뻐하며 돌아와 이르되</p><h1>칠십 명의 제자들</h1><h3>주여 주의 이름이면 귀신들도 우리에게 항복하더이다</h3><p><u>18</u>예수께서 이르시되</p><h1>예수님</h1><h2>사탄이 하늘로부터 번개 같이 떨어지는 것을 내가 보았노라 <u>19</u>내가 너희에게 뱀과 전갈을 밟으며 원수의 모든 능력을 제어할 권능을 주었으니 너희를 해칠 자가 결코 없으리라 <u>20</u>그러나 귀신들이 너희에게 항복하는 것으로 기뻐하지 말고 너희 이름이 하늘에 기록된 것으로 기뻐하라</h2><p>하시니라<br />   <u>21</u>그 때에 예수께서 성령으로 기뻐하시며 이르시되</p><h1>예수님</h1><h2>천지의 주재이신 아버지여 이것을 지혜롭고 슬기 있는 자들에게는 숨기시고 어린 아이들에게는 나타내심을 감사하나이다 옳소이다 이렇게 된 것이 아버지의 뜻이니이다<br />   <u>22</u>내 아버지께서 모든 것을 내게 주셨으니 아버지 외에는 아들이 누구인지 아는 자가 없고 아들과 또 아들의 소원대로 계시를 받는 자 외에는 아버지가 누구인지 아는 자가 없나이다</h2><p>하시고 <u>23</u>제자들을 돌아 보시며 조용히 이르시되</p><h1>예수님</h1><h2>너희가 보는 것을 보는 눈은 복이 있도다 <u>24</u>내가 너희에게 말하노니 많은 선지자와 임금이 너희가 보는 바를 보고자 하였으되 보지 못하였으며 너희가 듣는 바를 듣고자 하였으되 듣지 못하였느니라</h2><p><u>25</u>어떤 율법교사가 일어나 예수를 시험하여 이르되</p><h1>어떤 율법교사</h1><h4>선생님 내가 무엇을 하여야 영생을 얻으리이까</h4><p><u>26</u>예수께서 이르시되</p><h1>예수님</h1><h2>율법에 무엇이라 기록되었으며 네가 어떻게 읽느냐</h2><p><u>27</u>대답하여 이르되</p><h1>어떤 율법교사</h1><h4>네 마음을 다하며 목숨을 다하며 힘을 다하며 뜻을 다하여 주 너의 하나님을 사랑하고 또한 네 이웃을 네 자신 같이 사랑하라 하였나이다</h4><p><u>28</u>예수께서 이르시되</p><h1>예수님</h1><h2>네 대답이 옳도다 이를 행하라 그러면 살리라</h2><p>하시니 <u>29</u>그 사람이 자기를 옳게 보이려고 예수께 여짜오되</p><h1>어떤 율법교사</h1><h4>그러면 내 이웃이 누구니이까</h4><p><u>30</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>어떤 사람이 예루살렘에서 여리고로 내려가다가 강도를 만나매 강도들이 그 옷을 벗기고 때려 거의 죽은 것을 버리고 갔더라<br />   <u>31</u>마침 한 제사장이 그 길로 내려가다가 그를 보고 피하여 지나가고 <u>32</u>또 이와 같이 한 레위인도 그 곳에 이르러 그를 보고 피하여 지나가되<u>33</u>어떤 사마리아 사람은 여행하는 중 거기 이르러 그를 보고 불쌍히 여겨 <u>34</u>가까이 가서 기름과 포도주를 그 상처에 붓고 싸매고 자기 짐승에 태워 주막으로 데리고 가서 돌보아 주니라<br />   <u>35</u>그 이튿날 그가 주막 주인에게 데나리온 둘을 내어 주며 이르되 이 사람을 돌보아 주라 비용이 더 들면 내가 돌아올 때에 갚으리라 하였으니 <u>36</u>네 생각에는 이 세 사람 중에 누가 강도 만난 자의 이웃이 되겠느냐</h2><p> <u>37</u>이르되</p><h1>어떤 율법교사</h1><h4>자비를 베푼 자니이다</h4><p>예수께서 이르시되</p><h1>예수님</h1><h2>가서 너도 이와 같이 하라</h2><p>하시니라<br />   <u>38</u>그들이 길 갈 때에 예수께서 한 마을에 들어가시매 마르다라 이름하는 한 여자가 자기 집으로 영접하더라 <u>39</u>그에게 마리아라 하는 동생이 있어 주의 발치에 앉아 그의 말씀을 듣더니 <u>40</u>마르다는 준비하는 일이 많아 마음이 분주한지라 예수께 나아가 이르되</p><h1>마르다</h1><h4>주여 내 동생이 나 혼자 일하게 두는 것을 생각하지 아니하시나이까 그를 명하사 나를 도와 주라 하소서</h4><p><u>41</u>주께서 대답하여 이르시되</p><h1>예수님</h1><h2>마르다야 마르다야 네가 많은 일로 염려하고 근심하나 <u>42</u>몇 가지만 하든지 혹은 한 가지만이라도 족하니라 마리아는 이 좋은 편을 택하였으니 빼앗기지 아니하리라</h2><p>하시니라<br />   <strong>11</strong><u>1</u>예수께서 한 곳에서 기도하시고 마치시매 제자 중 하나가 여짜오되</p><h1>제자중 한 사람</h1><h3>주여 요한이 자기 제자들에게 기도를 가르친 것과 같이 우리에게도 가르쳐 주옵소서</h3><p><u>2</u>예수께서 이르시되</p><h1>예수님</h1><h2>너희는 기도할 때에 이렇게 하라<br />   아버지여 이름이 거룩히 여김을 받으시오며<br />   나라가 임하시오며<br />   <u>3</u>우리에게 날마다 일용할 양식을 주시옵고<br />   <u>4</u>우리가 우리에게 죄 지은 모든 사람을 용서하오니<br />   우리 죄도 사하여 주시옵고<br />   우리를 시험에 들게 하지 마시옵소서</h2><p>하라<br />   <u>5</u>또 이르시되</p><h1>예수님</h1><h2>너희 중에 누가 벗이 있는데 밤중에 그에게 가서 말하기를 벗이여 떡 세 덩이를 내게 꾸어 달라 <u>6</u>내 벗이 여행중에 내게 왔으나 내가 먹일 것이 없노라 하면 <u>7</u>그가 안에서 대답하여 이르되 나를 괴롭게 하지 말라 문이 이미 닫혔고 아이들이 나와 함께 침실에 누웠으니 일어나 네게 줄 수가 없노라 하겠느냐 <u>8</u>내가 너희에게 말하노니 비록 벗 됨으로 인하여서는 일어나서 주지 아니할지라도 그 간청함을 인하여 일어나 그 요구대로 주리라<br />   <u>9</u>내가 또 너희에게 이르노니 구하라 그러면 너희에게 주실 것이요 찾으라 그러면 찾아낼 것이요 문을 두드리라 그러면 너희에게 열릴 것이니 <u>10</u>구하는 이마다 받을 것이요 찾는 이는 찾아낼 것이요 두드리는 이에게는 열릴 것이니라<br />   <u>11</u>너희 중에 아버지 된 자로서 누가 아들이 생선을 달라 하는데 생선 대신에 뱀을 주며 <u>12</u>알을 달라 하는데 전갈을 주겠느냐 <u>13</u>너희가 악할지라도 좋은 것을 자식에게 줄 줄 알거든 하물며 너희 하늘 아버지께서 구하는 자에게 성령을 주시지 않겠느냐</h2><p>하시니라<br />   <u>14</u>예수께서 한 말 못하게 하는 귀신을 쫓아내시니 귀신이 나가매 말 못하는 사람이 말하는지라 무리들이 놀랍게 여겼으나 <u>15</u>그 중에 더러는 말하기를</p><h1>군중들</h1><h4>그가 귀신의 왕 바알세불을 힘입어 귀신을 쫓아낸다</h4><p>하고 <u>16</u>또 더러는 예수를 시험하여 하늘로부터 오는 표적을 구하니<br />   <u>17</u>예수께서 그들의 생각을 아시고 이르시되</p><h1>예수님</h1><h2>스스로 분쟁하는 나라마다 황폐하여지며 스스로 분쟁하는 집은 무너지느니라 <u>18</u>너희 말이 내가 바알세불을 힘입어 귀신을 쫓아낸다 하니 만일 사탄이 스스로 분쟁하면 그의 나라가 어떻게 서겠느냐 <u>19</u>내가 바알세불을 힘입어 귀신을 쫓아내면 너희 아들들은 누구를 힘입어 쫓아내느냐 그러므로 그들이 너희 재판관이 되리라 <u>20</u>그러나 내가 만일 하나님의 손을 힘입어 귀신을 쫓아낸다면 하나님의 나라가 이미 너희에게 임하였느니라 <u>21</u>강한 자가 무장을 하고 자기 집을 지킬 때에는 그 소유가 안전하되 <u>22</u>더 강한 자가 와서 그를 굴복시킬 때에는 그가 믿던 무장을 빼앗고 그의 재물을 나누느니라<br />   <u>23</u>나와 함께 하지 아니하는 자는 나를 반대하는 자요 나와 함께 모으지 아니하는 자는 헤치는 자니라<br />   <u>24</u>더러운 귀신이 사람에게서 나갔을 때에 물 없는 곳으로 다니며 쉬기를 구하되 얻지 못하고 이에 이르되 내가 나온 내 집으로 돌아가리라 하고 <u>25</u>가서 보니 그 집이 청소되고 수리되었거늘 <u>26</u>이에 가서 저보다 더 악한 귀신 일곱을 데리고 들어가서 거하니 그 사람의 나중 형편이 전보다 더 심하게 되느니라</h2><p><u>27</u>이 말씀을 하실 때에 무리 중에서 한 여자가 음성을 높여 이르되</p><h1>무리중 한 여인</h1><h4>당신을 밴 태와 당신을 먹인 젖이 복이 있나이다</h4><p>하니 <u>28</u>예수께서 이르시되</p><h1>예수님</h1><h2>오히려 하나님의 말씀을 듣고 지키는 자가 복이 있느니라</h2><p>하시니라<br />   <u>29</u>무리가 모였을 때에 예수께서 말씀하시되</p><h1>예수님</h1><h2>이 세대는 악한 세대라 표적을 구하되 요나의 표적 밖에는 보일 표적이 없나니 <u>30</u>요나가 니느웨 사람들에게 표적이 됨과 같이 인자도 이 세대에 그러하리라<br />   <u>31</u>심판 때에 남방 여왕이 일어나 이 세대 사람을 정죄하리니 이는 그가 솔로몬의 지혜로운 말을 들으려고 땅 끝에서 왔음이거니와 솔로몬보다 더 큰 이가 여기 있으며 <u>32</u>심판 때에 니느웨 사람들이 일어나 이 세대 사람을 정죄하리니 이는 그들이 요나의 전도를 듣고 회개하였음이거니와 요나보다 더 큰 이가 여기 있느니라<br />   <u>33</u>누구든지 등불을 켜서 움 속에나 말 아래에 두지 아니하고 등경 위에 두나니 이는 들어가는 자로 그 빛을 보게 하려 함이라 <u>34</u>네 몸의 등불은 눈이라 네 눈이 성하면 온 몸이 밝을 것이요 만일 나쁘면 네 몸도 어두우리라 <u>35</u>그러므로 네 속에 있는 빛이 어둡지 아니한가 보라 <u>36</u>네 온 몸이 밝아 조금도 어두운 데가 없으면 등불의 빛이 너를 비출 때와 같이 온전히 밝으리라</h2><p>하시니라<br />   <u>37</u>예수께서 말씀하실 때에 한 바리새인이 자기와 함께점심 잡수시기를 청하므로 들어가 앉으셨더니 <u>38</u>잡수시기 전에 손 씻지 아니하심을 그 바리새인이 보고 이상히 여기는지라 <u>39</u>주께서 이르시되</p><h1>예수님</h1><h2>너희 바리새인은 지금 잔과 대접의 겉은 깨끗이 하나 너희 속에는 탐욕과 악독이 가득하도다 <u>40</u>어리석은 자들아 겉을 만드신 이가 속도 만들지 아니하셨느냐 <u>41</u>그러나 그 안에 있는 것으로 구제하라 그리하면 모든 것이 너희에게 깨끗하리라<br />   <u>42</u>화 있을진저 너희 바리새인이여 너희가 박하와 운향과 모든 채소의 십일조는 드리되 공의와 하나님께 대한 사랑은 버리는도다 그러나 이것도 행하고 저것도 버리지 말아야 할지니라<br />   <u>43</u>화 있을진저 너희 바리새인이여 너희가 회당의 높은 자리와 시장에서 문안 받는 것을 기뻐하는도다 <u>44</u>화 있을진저 너희여 너희는 평토장한 무덤 같아서 그 위를 밟는 사람이 알지 못하느니라</h2><p><u>45</u>한 율법교사가 예수께 대답하여 이르되</p><h1>어떤 율법교사</h1><h4>선생님 이렇게 말씀하시니 우리까지 모욕하심이니이다</h4><p><u>46</u>이르시되</p><h1>예수님</h1><h2>화 있을진저 또 너희 율법교사여 지기 어려운 짐을 사람에게 지우고 너희는 한 손가락도 이 짐에 대지 않는도다 <u>47</u>화 있을진저 너희는 선지자들의 무덤을 만드는도다 그들을 죽인 자도 너희 조상들이로다 <u>48</u>이와 같이 그들은 죽이고 너희는 무덤을 만드니 너희가 너희 조상의 행한 일에 증인이 되어 옳게 여기는도다 <u>49</u>그러므로 하나님의 지혜가 일렀으되 내가 선지자와 사도들을 그들에게 보내리니 그 중에서 더러는 죽이며 또 박해하리라 하였느니라<br />   <u>50</u>창세 이후로 흘린 모든 선지자의 피를 이 세대가 담당하되 <u>51</u>곧 아벨의 피로부터 제단과 성전 사이에서 죽임을 당한 사가랴의 피까지 하리라 내가 너희에게 이르노니 과연 이 세대가 담당하리라<br />   <u>52</u>화 있을진저 너희 율법교사여 너희가 지식의 열쇠를 가져가서 너희도 들어가지 않고 또 들어가고자 하는 자도 막았느니라</h2><p>하시니라<br />   <u>53</u>거기서 나오실 때에 서기관과 바리새인들이 거세게 달려들어 여러 가지 일을 따져 묻고 <u>54</u>그 입에서 나오는 말을 책잡고자 하여 노리고 있더라</p>","path":"/331","Question1":"예수님께서 예루살렘으로 여행하실 때 사람들이 다른 방식으로 예수님께 응답하였던 것을 주목하십시오.","Question2":"이 응답자들 중 한 사람과 동일시할 수 있습니까? 만약에 그렇다면, 어떻게?","Question3":"예수님에 의하면, 누가 \"이웃\"이고 그들을 사랑한다는 것은 어떤 모습입니까? ","Question4":"제자들은 예수님에게 기도에 대해 가르쳐달라고 했습니다. 예수님의 응답에서 당신은 무엇을 배울 수 있습니까?","prevPath":"/330","nextPath":"/332"}},"pathContext":{}}

